*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
  color: beige;
}

body{

  background-image: linear-gradient(to bottom, #ffffff, #404040 10%, #5f00ff);;
 
  
}
header{
  background-color: #5f00ff;
  width: 100%;
  height: 5rem;
  
}
nav{
  
  width: 100%;
  height: 80px;
}

nav ul{
  float: right;
  margin-right: 20px;
   
  
}

nav ul li{
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
   
}
nav ul li a{
  color: rgb(255, 255, 255);
  font-size: 18px;
  padding: 7px 13px;
  border: 3px;
}

li a.active, li a:hover{
  background: #FED69C;
  color: #052D3F;
  transition: .5s;
}

.enlace{
  position: absolute;
  padding: 20px 50px;
}
.logo{
  height: 40px;
  background-color: white;
  border-radius: 50%;
}


.checkBtn{
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  display: none;
}

#check{
  display: none;
}


.wellcomezone{
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  border: 1px solid black;
  align-items: center;
  flex-wrap: wrap;
}
.wellcomezone div{
  display: flex;
  justify-content: center;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 30%;
  height: 25rem;
  border: 1px solid black;
}
.wellcometext, h3, p{
  padding: 1rem 1rem;  
}
.wellcometext button{
  padding: 1rem 1rem; 
  background-color: A0FF00; 
}
.zonatarjeta1{
  width: 100%;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15rem;
}
.phone{
  height: 25rem;
  width: 60%;
}

.tarjeta1{
  display: flex;
  justify-content: center;
  width: 80%;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 15rem;
}
.subtarjetazone{
  display: flex;
  justify-content: center;
  width: 80%;
  border: 1px solid black;
}
.subtarjeta{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  border: 1px solid black;
}
.records{
  display: flex;
  justify-content: space-around;
  width: 80%;
  border: 1px solid black;
  display: flex;
  align-items: center;
  height: 5rem;
}

.diagonal{
  border: 1px solid black;
  width: 20rem;
  height: 50rem;
  rotate: -25deg;
  top: 5rem;
  left: 30%;
  z-index: -1;
  position: absolute;
  background-image: linear-gradient(to bottom right, #ffffff, #5f00ff);
  border: none;

}




 